.taskWrapper {
    background-color: #ccc;
    border-radius: 10px;
    box-shadow: 0 0 14px 1px rgba(0, 0, 0, 0.2);
    padding: 20px;
    margin-bottom: 20px;
}

.taskHeading {
    font-size: 24px;
    margin-bottom: 15px;
    text-decoration: none;
    color: black;
}

.taskDescription {
    font-size: 16px;
    text-decoration: none;
    color: black;
}

